.tools {
  @media (min-width: @tablet) {
    display: flex;
  }

  @media (min-width: @desktop) {
    display: flex;
  }
}

.tools__post-button {
  color: @red;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  border: 3px solid #D22856;
  border-radius: 30px;
}

.tools__reset-button {
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  opacity: 0.1;
  border: 3px solid #283645;
  border-radius: 30px;
}
