.form__name {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
    padding: 0 130px;
  }
}

.form__footer,
.form__emotions,
.form__contacts {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
    padding: 0 130px;
  }
}

.form__note {
  display: block;
  text-transform: uppercase;
}

.form__note-mark {
  color: @red;
}
