.gallery__flex {
  @media (min-width: @tablet) {
    width: 620px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @media (min-width: @desktop) {
    width: 940px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.gallery__item {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 300px;
  }

  @media (min-width: @desktop) {
    width: 300px;
  }
}

.gallery__top-item-info {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
  }
}
