.gallery-post__top-post {
  color: @white;
  font-weight: bold;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  background-color: @red;
}

.gallery-post__img {
  width: 100%;
}

.gallery-post__author {
  font-weight: bold;
  line-height: 24px;
}

.gallery-post__time-posted {
  font-weight: 300;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
}

.gallery-post__comment {
  line-height: 24px;
}

.gallery-post__likes,
.gallery-post__likes-number {
  color: @red;
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
}
