.achievements {
  background-color: @grey;
  background-size: 100%;
}

.achievements__fieldset {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
    padding: 0 130px;
  }
}

.achievements__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.achievements__title {
  color: @red;
  line-height: 18px;
  text-transform: uppercase;
}

.achievements__input {
  font-size: 18px;
}
