.app {
  background-color: @grey;
}

.app__fieldset {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
    padding: 0 130px;
  }
}

.app__title {
  color: @red;
  line-height: 18px;
  text-transform: uppercase;
}

.app__list {
  list-style: none;
}

.app__option {
  font-size: 18px;
}
