.page-form-main__title-wrapper {
  background-color: @dark-blue;
  background-image: url("/img/back-mobile-clear.png");
  background-repeat: no-repeat;
  background-position: center;
  text-align: center;
  background-size: 100%;

  @media (min-width: @tablet) {
    background-image: url("/img/back-tablet-clear.png");
  }

  @media (min-width: @desktop) {
    background-image: url("/img/back-desktop-clear.png");
  }
}

.page-form-main__title {
  width: 280px;
  padding: 0 20px;
  margin: 0 auto;
  color: @white;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
    padding: 0 130px;
  }
}

.page-form-main__description {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
    padding: 0 130px;
  }
}
