.page-catalog-main__title {
  margin: 0;
  color: @white;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
}

.page-catalog-main__title-wrapper {
  background-color: @dark-blue;
  background-image: url("/img/back-mobile-clear.png");
  background-repeat: no-repeat;
  background-position: center;
  text-align: center;
  background-size: 100%;

  @media (min-width: @tablet) {
    background-image: url("/img/back-tablet-clear.png");
  }

  @media (min-width: @tablet) {
    background-image: url("/img/back-desktop-clear.png");
  }
}

.page-catalog-main__slogan,
.page-catalog-main__intro {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
  }
}

.page-catalog-main__photo-tools {
  background-color: @grey;

  @media (min-width: @desktop) {
    background-color: transparent;
  }
}

.page-catalog-main__tools-wrapper {
  width: 280px;
  margin: 0 auto;

  @media (min-width: @tablet) {
    width: 620px;
  }

  @media (min-width: @desktop) {
    width: 940px;
    background-color: @grey;
  }
}
